<template>
  <div class="login">
    <div class="row mt-3">
      <div class="card mx-auto">
        <div class="card-header text-white bg-primary">Login</div>
        <div class="card-body">
          <form @submit.prevent="loginUser">
            <div class="form-group">
              <label for="username">Username</label>
              <input
                type="text"
                placeholder="Username"
                name="username"
                v-model="username"
                class="form-control"
                id="username"
                autocomplete="off"
                required
              />
            </div>
            <div class="form-group">
              <label for="password">Password</label>
              <input
                type="password"
                placeholder="Password"
                name="password"
                v-model="password"
                class="form-control"
                id="password"
                autocomplete="off"
                required
              />
            </div>
            <input type="submit" value="Login" class="btn btn-primary btn-block" />
            <!-- &nbsp;&nbsp;&nbsp;&nbsp; -->
            <router-link to="/register" class="card-link">Don't have an account?</router-link>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import { mapActions } from "vuex";

export default {
  name: "Login",
  components: {},
  data() {
    return {
      username: "",
      password: "",
    };
  },
  methods: {
    ...mapActions(["login"]),
    loginUser() {
      let user = {
        username: this.username,
        password: this.password,
      };
      this.login(user)
      .then(res => {
        if(res.data.success){
          this.$router.push('/profile')
        }
      })
      .catch(err => {
        console.log(err)
      })
    },
  },
};
</script>
<style scoped>
.login{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.card {
  width: 60vh;
}
</style>
